@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background-color: rgba(83, 0, 83, 0.233);
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(0, 21, 143);
  color: white;
  font-family: "Press Start 2P", cursive;
}

#titleMessage {
  padding-top: 10px;
}

#scoresContainer {
  position: relative;
  left: 350px;
  padding-top: 20px;
  padding-bottom: 20px;
}

#highScore {
  padding-bottom: 10px;
}

#appGrid {
  display: flex;
  justify-content: center;
}

#gameGrid {
  display: flex;
  background-image: url(/Memory-Game/static/media/pokemonMap.02e62657.jpg);
  width: -webkit-max-content;
  width: max-content;
  padding-bottom: 136px;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.card {
  font-family: "Press Start 2P", cursive;
  font-size: 12px;
  border: 2px solid black;
  width: 200px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 70px;
  margin-left: 30px;
  margin-right: 30px;
  background-color: rgb(248, 196, 53);
  transition: all 0.2s ease-in-out;
}

.card:hover {
  /* box-shadow: 0px 10px 10px 2px rgb(210, 233, 109); */
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.cardImage {
  width: 200px;
  height: 250px;
  border: 2px solid black;
  border-top: none;
}

